/* Global SCSS partial */
html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Segoe UI", "Inter", sans-serif;
}

section {
  padding: 2em 2em 0;
}

ul,
li {
  padding: 0;
}

th,
td {
  margin: 1em;
  padding: 0.5em;
}

th {
  border-radius: 10px 10px 0 0;
}

.tab-date {
  display: flex;
  justify-content: space-between;
}

.tab-width-sm {
  min-width: 8rem;
}

.tab-width-med {
  min-width: 13rem;
}

.icon {
  margin-left: 0.75rem;
}

.new-btn {
  background-color: #27B1F3;
  width: 8rem;
  padding: 0.5rem;
  border-radius: 4px;
  display: flex;
  justify-content: space-evenly;
  margin: auto;
}

h1, h2, h3, h4, h5, h6, th {
  line-height: 1.1;
  margin-top: 0;
  margin-block-start: 0;
  margin-block-end: 0;
  font-weight: 0;
}

p {
  font-weight: 500;
  margin-top: 0;
  margin-block-start: 0;
  margin-block-end: 0;
}

.text-left {
  text-align: start;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: end;
}

.light {
  font-weight: 400;
}

.regular {
  font-weight: 600;
}

.bold {
  font-weight: 700;
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --dark-grey-color: #303133;
  --mid-grey-color: #505676;
  ---mid-light-grey-color: #BDBEC2;
  --light-grey-color: #F4F5F7;
  --white-color: #fff;
  /* Stage colors */
  --dark-green-color: #047243;
  --mid-blue-color: #007BB7;
  --purple-color: #6B43C8;
  --light-green-color: #04B767;
  /* Priority colors */
  --red-color: #FD582A;
  --light-blue-color: #5CC5FF;
}

table {
  color: var(--mid-grey-color);
  border-color: var(--white-color);
}

th {
  color: var(--dark-grey-color);
}

.white-text {
  color: var(--white-color);
}

.tab-color {
  background-color: var(--light-grey-color);
}

.deals-table__content-title, .deals-table__content-deal-tab {
  color: var(--dark-green-color);
}

.closed-table__content-title, .closed-table__content-deal-tab {
  color: var(--light-green-color);
}

a, a:visited, a:active {
  color: var(--dark-grey-color);
}

a:hover {
  color: var(--mid-grey-color);
}

.nav-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5em 5em 0 5em;
}
.nav-menu__item {
  list-style-type: none;
  display: flex;
}
.nav-menu__links {
  align-items: center;
  display: flex;
  padding: 0 2rem;
}
.nav-menu__hamburger {
  position: absolute;
  top: 3.4em;
  right: 2em;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 21px;
}
@media (max-width: 414px) {
  .nav-menu {
    flex-direction: column;
    align-items: flex-start;
    padding: 2em 2em 0 2em;
    display: block !important;
  }
  .nav-menu__header {
    padding: 3em 2em 0;
  }
  .nav-menu__item {
    display: none;
  }
  .nav-menu__hamburger {
    display: flex;
    padding: 1.6em 2em 0;
  }
  .nav-menu__links {
    text-align: center;
    width: 100%;
    display: inline-block;
    flex-direction: column;
    padding: 1em 2em;
  }
}

.deals-table {
  padding: 2em 5em 0;
  overflow: auto;
}
.deals-table__menu {
  display: flex;
  justify-content: end;
  align-items: center;
  padding: 5em 5em 0 5em;
}
.deals-table__menu-item {
  list-style-type: none;
  display: inline-block;
  cursor: pointer;
}
.deals-table__menu-links {
  padding: 0 1em 0 1em;
  display: block;
}
.deals-table__menu-item-filter {
  display: none;
  border: none !important;
}
.deals-table__menu-item-filter-links {
  position: absolute;
  margin-top: 1em;
  padding: 0 1em 0 1em;
  width: 12em;
  background-color: #fff;
  border: 1px solid;
  display: block;
}
.deals-table__menu-item-filter-text {
  display: flex;
  justify-content: space-between;
  padding: 0.5em 0;
}
.deals-table__menu-mobile {
  display: none;
}
.deals-table__content {
  width: 100%;
}
.deals-table__content-title {
  padding-bottom: 1em;
}
.deals-table__content-company {
  width: 10em;
  color: var(--mid-grey-color);
}
.deals-table__content-company-name {
  border-color: var(--dark-green-color);
  border-left-style: solid;
  border-width: 0.8em;
}
.deals-table__content-user-image {
  width: 40px;
  height: auto;
  clip-path: circle(30%);
  -webkit-clip-path: circle(40%);
}
.deals-table__content-total {
  background-color: var(--light-grey-color);
}
@media (max-width: 414px) {
  .deals-table__menu {
    display: none;
  }
  .deals-table__menu-mobile {
    display: flex;
    align-items: center;
    padding: 5em 3em 1em 3em;
    justify-content: center;
  }
  .deals-table__menu-item {
    width: 50px;
  }
  .deals-table__content {
    overflow: auto;
  }
  .deals-table__content-user-image {
    width: 100%;
    height: auto;
  }
}

.closed-table {
  padding: 2em 5em 0;
  overflow: auto;
}
.closed-table__menu {
  display: flex;
  justify-content: end;
  align-items: center;
  padding: 5em 5em 0 5em;
}
.closed-table__menu-item {
  list-style-type: none;
  display: inline-block;
}
.closed-table__menu-links {
  padding: 0 1em 0 1em;
  display: block;
}
.closed-table__menu-mobile {
  display: none;
  padding: 2em 2em 0;
}
.closed-table__content {
  width: 100%;
}
.closed-table__content-title {
  padding-bottom: 1em;
}
.closed-table__content-company {
  width: 10em;
  color: #505676;
}
.closed-table__content-company-name {
  border-color: var(--light-green-color);
  border-left-style: solid;
  border-width: 0.8em;
}
.closed-table__content-priorityText {
  color: #fff;
}
.closed-table__content-stageText {
  color: #fff;
  text-align: center;
}
.closed-table__content-stageTab {
  background-color: #F4F5F7;
}
.closed-table__content-user-image {
  width: 40px;
  height: auto;
  clip-path: circle(30%);
  -webkit-clip-path: circle(40%);
}
.closed-table__content-total {
  background-color: var(--light-grey-color);
}
@media (max-width: 414px) {
  .closed-table__menu {
    display: none;
  }
  .closed-table__menu-mobile {
    display: flex;
    align-items: center;
    padding: 5em 3em 1em 3em;
    justify-content: center;
  }
  .closed-table__menu-item {
    width: 50px;
  }
  .closed-table__content {
    overflow: auto;
  }
  .closed-table__content-user-image {
    width: 100%;
    height: auto;
  }
}

