@forward 'globals';

.nav-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5em 5em 0 5em;

  &__item {
    list-style-type: none;
    display: flex;
  }

  &__links {
    align-items: center;
    display: flex;
    padding: 0 2rem;
  }

  &__hamburger {
    position: absolute;
    top: 3.4em;
    right: 2em;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 21px;
  }

  @media (max-width: 414px) {
    flex-direction: column;
    align-items: flex-start;
    padding: 2em 2em 0 2em;
    display: block !important;

    &__header {
      padding: 3em 2em 0;
    }

    &__item {
      display: none;
    }

    &__hamburger {
      display: flex;
      padding: 1.6em 2em 0;
    }

    &__links {
      text-align: center;
      width: 100%;
      display: inline-block;
      flex-direction: column;
      padding: 1em 2em;
    }
  }
}


// Deals Table
.deals-table {
  padding: 2em 5em 0;
  overflow: auto;

  &__menu {
    display: flex;
    justify-content: end;
    align-items: center;
    padding: 5em 5em 0 5em;
  }

  &__menu-item {
    list-style-type: none;
    display: inline-block;
    cursor: pointer;
  }

  &__menu-links {
    padding: 0 1em 0 1em;
    display: block;
  }

  &__menu-item-filter {
    display: none;
    border: none !important;
  }

  &__menu-item-filter-links {
    position: absolute;
    margin-top: 1em;
    padding: 0 1em 0 1em;
    width: 12em;
    background-color: #fff;
    border: 1px solid;
    display: block;
  }

  &__menu-item-filter-text {
    display: flex;
    justify-content: space-between;
    padding: 0.5em 0;
  }

  &__menu-mobile {
    display: none;
  }

  &__content {
    width: 100%;
  }

  &__content-title {
    padding-bottom: 1em;
  }

  &__content-company {
    width: 10em;
    color: var(--mid-grey-color);
  }

  &__content-company-name {
    border-color: var(--dark-green-color);
    border-left-style: solid;
    border-width: 0.8em;
  }

  &__content-user-image {
    width: 40px;
    height: auto;
    clip-path: circle(30%);
    -webkit-clip-path: circle(40%);
  }

  &__content-total {
    background-color: var(--light-grey-color);
  }

  @media (max-width: 414px) {
    &__menu {
      display: none;
    }

    &__menu-mobile {
      display: flex;
      align-items: center;
      padding: 5em 3em 1em 3em;
      justify-content: center;
    }

    &__menu-item {
      width: 50px;
    }

    &__content {
      overflow: auto;
    }

    &__content-user-image {
      width: 100%;
      height: auto;
    }
  }
}


