/* Global SCSS partial */

html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', 'Inter', sans-serif;
}

section {
  padding: 2em 2em 0;
}

ul,
li {
  padding: 0;
}

// Deals, Closed Won and Board Summary Tables
th,
td {
  margin: 1em;
  padding: 0.5em;
}

th {
  border-radius: 10px 10px 0 0;
}

.tab-date {
  display: flex;
  justify-content: space-between;
}

// Margins, Padding & Width
.tab-width-sm {
  min-width: 8rem;
}

.tab-width-med {
  min-width: 13rem;
}

// Icons and buttons
.icon {
  margin-left: 0.75rem;
}

.new-btn {
  background-color: #27B1F3;
    width: 8rem;
    padding: 0.5rem;
    border-radius: 4px;
    display: flex;
    justify-content: space-evenly;
    margin: auto;
}